.App {
  height: 100vh;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: black;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  overflow-y: hidden;
  /* border: 2px solid rgba(255, 0, 0, 0); */
}
.app_main {
  margin: auto;
  font-family: 'PT Mono', monospace;
  background: -webkit-gradient(linear,left top, right bottom,from(rgb(29, 5, 243)),to(rgb(0, 0, 0)));
  background: -webkit-linear-gradient(top left,rgb(29, 5, 243),rgb(0, 0, 0));
  background: -o-linear-gradient(top left,rgb(29, 5, 243),rgb(0, 0, 0));
  background: linear-gradient(to bottom right,rgb(29, 5, 243),rgb(0, 0, 0));
  border: 5px solid rgb(216, 216, 216);
  border-radius: 12px;
  position: absolute;
  width: 1000px;
  height: 800px;
}



@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes temp {
  50% {-webkit-box-shadow: 3px 3px 45px 20px #00ffffc2;box-shadow: 3px 3px 45px 20px #00ffffc2;}
}

@keyframes temp {
  50% {-webkit-box-shadow: 3px 3px 45px 20px #00ffffc2;box-shadow: 3px 3px 45px 20px #00ffffc2;}
}

@-webkit-keyframes bgblue {
  50% {background:rgb(0, 238, 255);}
}

@keyframes bgblue {
  50% {background:rgb(0, 238, 255);}
}


